export default {
	data () {
		return {
			scrollList:['全部消息','活动福利','业务须知'],
			currentIndex: 0,
			//参数用下标
			ajaxIndex: '',
			//分页
			pageNum: 0,
			hasFlag:true,
			newsList: [],
			msgContent: [],
			readerFlag: ''
		}
	},
	onLoad() {
		this.initNewsList()
	
	},
	onUnload(){
		// this.initReadNews()
	},
	onShow() {
		uni.removeStorageSync('newRichContent');
		
	},
	onPullDownRefresh(){
		setTimeout(() => {
			this.pageNum = 0
			this.hasFlag = true
			this.newsList = []
			this.msgContent = []
			this.initNewsList()
			this.initReadNews()
			uni.stopPullDownRefresh()
		},1000)
		
	},
	methods: {
		//去订单
		goTonewsRich (title,content) {
			console.log(content)
			uni.setStorageSync('newRichContent',content);
			uni.navigateTo({
				url: './newsRich?title=' + title 
			})
		},
		//改下标
		handleCurrentIndex (index) {
			this.currentIndex = index
			
			if(this.currentIndex == 0) {
				this.ajaxIndex = ""
			} else if (this.currentIndex == 1) {
				this.ajaxIndex = 0
			}else if (this.currentIndex == 2) {
				this.ajaxIndex = 1
			}
			this.newsList = []
			this.msgContent = []
			this.pageNum = 0
			this.hasFlag = true
			this.initNewsList()
		},
		async initNewsList(){
			if(!this.hasFlag) return //没有更多数据
			this.pageNum = ++this.pageNum
			let res = await this.$fetch(this.$api.msgList,{msgType:this.ajaxIndex,pageNum:this.pageNum,pageSize:12},'POST','FORM')
	
			for( let i = 0 ; i < res.rows.length;i++) {
				this.msgContent.push(res.rows[i].content)
			}
		
			
			this.newsList = [...this.newsList,...res.rows]
			if(this.newsList.length) {
				
			
			this.newsList.forEach(item => {
				item.content = this.filterHTMLTag(item.content)
			})
			
			// console.log(JSON.stringify(this.newsList))
			this.readerFlag = this.newsList[0].id
		}
			
			this.hasFlag = this.pageNum * 12 < res.total
			this.initReadNews()
		},
		
		filterHTMLTag (msg) {
			var msg = msg.replace(/<\/?[^>]*>/g, ''); //去除HTML Tag
			msg = msg.replace(/[|]*\n/, '') //去除行尾空格
			msg = msg.replace(/&npsp;/ig, ''); //去掉npsp
			return msg; 
		},
		//阅读
		async initReadNews(){
			
			if (this.readerFlag !== "") {
				let res = await this.$fetch(this.$api.bindUserMsg,{msgId:this.readerFlag},'POST','FORM')
				console.log(res)
			}
			
		}
	},
	onReachBottom() { 
		if(this.hasFlag) {
			this.initNewsList()
		}
	}
}
